.new-expense__controls {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    margin-bottom: 1rem;
    text-align: left;
  }
  
  .new-expense__control label {
    font-weight: bold;
    margin-bottom: 0.5rem;
    display: block;
  }
  
  .new-expense__control input {
    font: inherit;
    padding: 0.5rem;
    border-radius: 15px;
    border: 1px solid #ccc;
    width: 20rem;
    max-width: 100%;
  }
  .new-expense__control button{
    font: inherit;
    cursor: pointer;
    width: 50px;
    height: 50px;
    background-color: #4649FF;
    color: white;
    font-size: 30px;
    border-radius: 50%;
    margin: 20px 5px 25px 15px;
    border:none;
  }
  .new-expense__control button:hover{
    background-color: white;
    color: #4649FF;
    border: 1px solid #4649FF;
  }
